'use client';

import { Project } from '@/data/projects';
import { motion } from 'framer-motion';
import Link from 'next/link';
import { ArrowLeftIcon } from '@heroicons/react/24/outline';

interface MilestoneDetailProps {
  project: Project;
  onBack: () => void;
}

const statusColors = {
  'Completed': 'bg-success text-white',
  'In Progress': 'bg-primary text-white',
  'Not Started': 'bg-gray-600 text-white',
};

export default function MilestoneDetail({ project, onBack }: MilestoneDetailProps) {
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="bg-background-darker rounded-lg p-8 shadow-lg"
    >
      <div className="flex justify-between items-start mb-6">
        <button 
          onClick={onBack}
          className="flex items-center gap-2 text-text-secondary hover:text-text-primary transition-colors"
        >
          <ArrowLeftIcon className="h-5 w-5" />
          <span>Back to Milestones</span>
        </button>
        
        <span className={`px-3 py-1 rounded-full text-sm ${statusColors[project.status]}`}>
          {project.status}
        </span>
      </div>
      
      <div className="flex items-center gap-3 mb-3">
        {project.icon && <span className="text-3xl">{project.icon}</span>}
        <h2 className="text-3xl font-bold text-text-primary">{project.title}</h2>
      </div>
      
      <div className="mb-6">
        <span className="px-3 py-1 bg-primary/10 rounded-md text-sm text-primary">Month {project.month}</span>
      </div>
      
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-text-primary mb-3">Description</h3>
        <p className="text-text-secondary">{project.description}</p>
      </div>
      
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-text-primary mb-3">Technologies</h3>
        <div className="flex flex-wrap gap-2">
          {project.tags.map((tag) => (
            <span
              key={tag}
              className="px-3 py-1 bg-background-dark rounded-md text-sm text-text-secondary"
            >
              {tag}
            </span>
          ))}
        </div>
      </div>
      
      <div className="mb-8">
        <h3 className="text-xl font-semibold text-text-primary mb-3">Key Learnings</h3>
        <ul className="list-disc pl-5 text-text-secondary">
          {project.keyLearnings && project.keyLearnings.map((learning, index) => (
            <li key={index} className="mb-2">{learning}</li>
          ))}
          {!project.keyLearnings && (
            <>
              <li className="mb-2">Understanding of fundamental concepts</li>
              <li className="mb-2">Implementation of best practices</li>
              <li className="mb-2">Performance optimization techniques</li>
              <li className="mb-2">Real-world problem-solving</li>
            </>
          )}
        </ul>
      </div>
      
      {project.githubLink && (
        <div className="mt-8">
          <Link
            href={project.githubLink}
            target="_blank"
            rel="noopener noreferrer"
            className="inline-flex items-center gap-2 px-4 py-2 rounded-md bg-primary hover:bg-primary-hover transition-colors text-white"
          >
            View on GitHub
          </Link>
        </div>
      )}
    </motion.div>
  );
} 