'use client';

import { useState } from 'react';
import Card from '@/components/ui/Card';
import { Project, projects } from '@/data/projects';
import { motion, AnimatePresence } from 'framer-motion';
import MilestoneDetail from './MilestoneDetail';
import { Status } from '@/components/ui/Card';

const statuses: Status[] = ['Completed', 'In Progress', 'Not Started'];
const months = [1, 2, 3, 4, 5];
const monthNames = ['Foundations', 'Transformers & Prompting', 'Fine-Tuning & Thesis Start', 'Deployment & Advanced Systems', 'Multi-Agent Systems & Thesis Finalization'];

export default function MilestonesGrid() {
  const [selectedFilter, setSelectedFilter] = useState<Status | 'All'>('All');
  const [selectedMonth, setSelectedMonth] = useState<number | 'All'>('All');
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);

  let filteredProjects = projects;
  
  // Apply status filter
  if (selectedFilter !== 'All') {
    filteredProjects = filteredProjects.filter(project => project.status === selectedFilter);
  }
  
  // Apply month filter
  if (selectedMonth !== 'All') {
    filteredProjects = filteredProjects.filter(project => project.month === selectedMonth);
  }

  const handleViewDetails = (project: Project) => {
    setSelectedProject(project);
  };

  const handleBack = () => {
    setSelectedProject(null);
  };

  const container = {
    hidden: { opacity: 0 },
    show: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const item = {
    hidden: { opacity: 0, y: 20 },
    show: { opacity: 1, y: 0 }
  };

  // Group projects by month if not filtering by month
  const groupedProjects = selectedMonth === 'All' 
    ? months.map(month => ({
        month,
        name: monthNames[month - 1],
        projects: filteredProjects.filter(project => project.month === month)
      }))
    : null;

  return (
    <div className="py-8">
      {!selectedProject ? (
        <div>
          {/* Filters */}
          <div className="mb-12">
            <h3 className="text-lg font-medium text-text-primary mb-4 text-center">Filter by Status</h3>
            <div className="flex flex-wrap justify-center gap-3 mb-8">
              <button
                onClick={() => setSelectedFilter('All')}
                className={`px-4 py-2 rounded-md text-sm font-medium transition-colors ${
                  selectedFilter === 'All'
                    ? 'bg-primary text-white'
                    : 'bg-background-darker text-text-secondary hover:text-white'
                }`}
              >
                All
              </button>
              {statuses.map((status) => (
                <button
                  key={status}
                  onClick={() => setSelectedFilter(status)}
                  className={`px-4 py-2 rounded-md text-sm font-medium transition-colors ${
                    selectedFilter === status
                      ? 'bg-primary text-white'
                      : 'bg-background-darker text-text-secondary hover:text-white'
                  }`}
                >
                  {status}
                </button>
              ))}
            </div>

            <h3 className="text-lg font-medium text-text-primary mb-4 text-center">Filter by Month</h3>
            <div className="flex flex-wrap justify-center gap-3 mb-8">
              <button
                onClick={() => setSelectedMonth('All')}
                className={`px-4 py-2 rounded-md text-sm font-medium transition-colors ${
                  selectedMonth === 'All'
                    ? 'bg-secondary text-white'
                    : 'bg-background-darker text-text-secondary hover:text-white'
                }`}
              >
                All Months
              </button>
              {months.map((month, index) => (
                <button
                  key={month}
                  onClick={() => setSelectedMonth(month)}
                  className={`px-4 py-2 rounded-md text-sm font-medium transition-colors ${
                    selectedMonth === month
                      ? 'bg-secondary text-white'
                      : 'bg-background-darker text-text-secondary hover:text-white'
                  }`}
                >
                  Month {month}: {monthNames[index]}
                </button>
              ))}
            </div>
          </div>

          {/* Projects Grid */}
          <AnimatePresence>
            {groupedProjects ? (
              // Grouped by month
              <div className="space-y-16">
                {groupedProjects.map(group => 
                  group.projects.length > 0 && (
                    <motion.div key={group.month} initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="mb-12">
                      <h2 className="text-2xl font-bold text-text-primary mb-8 flex items-center">
                        <span className="text-primary">Month {group.month}:</span>
                        <span className="ml-2">{group.name}</span>
                      </h2>
                      <motion.div
                        variants={container}
                        initial="hidden"
                        animate="show"
                        className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
                      >
                        {group.projects.map((project) => (
                          <motion.div key={project.id} variants={item}>
                            <Card
                              title={project.title}
                              description={project.description}
                              status={project.status}
                              githubLink={project.githubLink}
                              tags={project.tags}
                              detailsLink="#"
                              onViewDetails={() => handleViewDetails(project)}
                              month={project.month}
                              icon={project.icon}
                            />
                          </motion.div>
                        ))}
                      </motion.div>
                    </motion.div>
                  )
                )}
              </div>
            ) : (
              // Flat list for filtered month
              <motion.div
                variants={container}
                initial="hidden"
                animate="show"
                className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6"
              >
                {filteredProjects.map((project) => (
                  <motion.div key={project.id} variants={item}>
                    <Card
                      title={project.title}
                      description={project.description}
                      status={project.status}
                      githubLink={project.githubLink}
                      tags={project.tags}
                      detailsLink="#"
                      onViewDetails={() => handleViewDetails(project)}
                      month={project.month}
                      icon={project.icon}
                    />
                  </motion.div>
                ))}
              </motion.div>
            )}
          </AnimatePresence>
        </div>
      ) : (
        <MilestoneDetail project={selectedProject} onBack={handleBack} />
      )}
    </div>
  );
} 